'use client'

import { useState } from 'react'

interface Transaction {
  id: number
  date: string
  customerName: string
  items: TransactionItem[]
  total: number
  paymentMethod: string
  status: string
}

interface TransactionItem {
  productName: string
  quantity: number
  price: number
  subtotal: number
}

interface Product {
  id: number
  name: string
  price: number
  stock: number
}

export default function TransactionsPage() {
  const [transactions, setTransactions] = useState<Transaction[]>([
    {
      id: 1,
      date: '2024-01-15 10:30',
      customerName: 'Ibu Sari',
      items: [
        { productName: 'Beras Premium', quantity: 2, price: 15000, subtotal: 30000 },
        { productName: 'Minyak Goreng', quantity: 1, price: 25000, subtotal: 25000 }
      ],
      total: 55000,
      paymentMethod: 'Tunai',
      status: 'Selesai'
    },
    {
      id: 2,
      date: '2024-01-15 11:15',
      customerName: 'Pak Budi',
      items: [
        { productName: 'Susu UHT', quantity: 3, price: 8000, subtotal: 24000 },
        { productName: 'Roti Tawar', quantity: 2, price: 15000, subtotal: 30000 }
      ],
      total: 54000,
      paymentMethod: 'Transfer',
      status: 'Selesai'
    },
    {
      id: 3,
      date: '2024-01-15 14:20',
      customerName: 'Ibu Ani',
      items: [
        { productName: 'Telur Ayam', quantity: 1, price: 28000, subtotal: 28000 }
      ],
      total: 28000,
      paymentMethod: 'Tunai',
      status: 'Selesai'
    }
  ])

  const [products] = useState<Product[]>([
    { id: 1, name: 'Beras Premium', price: 15000, stock: 45 },
    { id: 2, name: 'Minyak Goreng', price: 25000, stock: 8 },
    { id: 3, name: 'Gula Pasir', price: 12000, stock: 30 },
    { id: 4, name: 'Telur Ayam', price: 28000, stock: 25 },
    { id: 5, name: 'Susu UHT', price: 8000, stock: 50 },
    { id: 6, name: 'Roti Tawar', price: 15000, stock: 12 }
  ])

  const [showNewTransaction, setShowNewTransaction] = useState(false)
  const [newTransaction, setNewTransaction] = useState({
    customerName: '',
    items: [] as TransactionItem[],
    paymentMethod: 'Tunai'
  })
  const [selectedProduct, setSelectedProduct] = useState('')
  const [quantity, setQuantity] = useState(1)

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('id-ID', {
      style: 'currency',
      currency: 'IDR',
      minimumFractionDigits: 0
    }).format(amount)
  }

  const addItemToTransaction = () => {
    const product = products.find(p => p.name === selectedProduct)
    if (!product || quantity <= 0) return

    const existingItemIndex = newTransaction.items.findIndex(item => item.productName === selectedProduct)
    
    if (existingItemIndex >= 0) {
      const updatedItems = [...newTransaction.items]
      updatedItems[existingItemIndex].quantity += quantity
      updatedItems[existingItemIndex].subtotal = updatedItems[existingItemIndex].quantity * product.price
      setNewTransaction({ ...newTransaction, items: updatedItems })
    } else {
      const newItem: TransactionItem = {
        productName: selectedProduct,
        quantity: quantity,
        price: product.price,
        subtotal: quantity * product.price
      }
      setNewTransaction({ ...newTransaction, items: [...newTransaction.items, newItem] })
    }

    setSelectedProduct('')
    setQuantity(1)
  }

  const removeItemFromTransaction = (index: number) => {
    const updatedItems = newTransaction.items.filter((_, i) => i !== index)
    setNewTransaction({ ...newTransaction, items: updatedItems })
  }

  const calculateTotal = () => {
    return newTransaction.items.reduce((total, item) => total + item.subtotal, 0)
  }

  const saveTransaction = () => {
    if (!newTransaction.customerName || newTransaction.items.length === 0) {
      alert('Mohon lengkapi data transaksi')
      return
    }

    const transaction: Transaction = {
      id: transactions.length + 1,
      date: new Date().toLocaleString('id-ID'),
      customerName: newTransaction.customerName,
      items: newTransaction.items,
      total: calculateTotal(),
      paymentMethod: newTransaction.paymentMethod,
      status: 'Selesai'
    }

    setTransactions([transaction, ...transactions])
    setNewTransaction({ customerName: '', items: [], paymentMethod: 'Tunai' })
    setShowNewTransaction(false)
    alert('Transaksi berhasil disimpan!')
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold text-gray-900">Transaksi Penjualan</h1>
        <button
          onClick={() => setShowNewTransaction(true)}
          className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 transition-colors"
        >
          Transaksi Baru
        </button>
      </div>

      {showNewTransaction && (
        <div className="bg-white p-6 rounded-lg shadow-md border">
          <h2 className="text-xl font-semibold text-gray-900 mb-4">Transaksi Baru</h2>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-6">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Nama Pelanggan
              </label>
              <input
                type="text"
                value={newTransaction.customerName}
                onChange={(e) => setNewTransaction({ ...newTransaction, customerName: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                placeholder="Masukkan nama pelanggan"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Metode Pembayaran
              </label>
              <select
                value={newTransaction.paymentMethod}
                onChange={(e) => setNewTransaction({ ...newTransaction, paymentMethod: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              >
                <option value="Tunai">Tunai</option>
                <option value="Transfer">Transfer</option>
                <option value="Kartu Debit">Kartu Debit</option>
              </select>
            </div>
          </div>

          <div className="mb-6">
            <h3 className="text-lg font-medium text-gray-900 mb-3">Tambah Barang</h3>
            <div className="flex gap-4 items-end">
              <div className="flex-1">
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Pilih Barang
                </label>
                <select
                  value={selectedProduct}
                  onChange={(e) => setSelectedProduct(e.target.value)}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                >
                  <option value="">Pilih barang...</option>
                  {products.map(product => (
                    <option key={product.id} value={product.name}>
                      {product.name} - {formatCurrency(product.price)} (Stock: {product.stock})
                    </option>
                  ))}
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Jumlah
                </label>
                <input
                  type="number"
                  min="1"
                  value={quantity}
                  onChange={(e) => setQuantity(parseInt(e.target.value) || 1)}
                  className="w-20 px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                />
              </div>
              <button
                onClick={addItemToTransaction}
                disabled={!selectedProduct}
                className="bg-green-600 text-white px-4 py-2 rounded-lg hover:bg-green-700 disabled:bg-gray-400 transition-colors"
              >
                Tambah
              </button>
            </div>
          </div>

          {newTransaction.items.length > 0 && (
            <div className="mb-6">
              <h3 className="text-lg font-medium text-gray-900 mb-3">Daftar Barang</h3>
              <div className="overflow-x-auto">
                <table className="min-w-full divide-y divide-gray-200">
                  <thead className="bg-gray-50">
                    <tr>
                      <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Barang</th>
                      <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Harga</th>
                      <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Jumlah</th>
                      <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Subtotal</th>
                      <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Aksi</th>
                    </tr>
                  </thead>
                  <tbody className="bg-white divide-y divide-gray-200">
                    {newTransaction.items.map((item, index) => (
                      <tr key={index}>
                        <td className="px-4 py-2 text-sm text-gray-900">{item.productName}</td>
                        <td className="px-4 py-2 text-sm text-gray-900">{formatCurrency(item.price)}</td>
                        <td className="px-4 py-2 text-sm text-gray-900">{item.quantity}</td>
                        <td className="px-4 py-2 text-sm text-gray-900">{formatCurrency(item.subtotal)}</td>
                        <td className="px-4 py-2">
                          <button
                            onClick={() => removeItemFromTransaction(index)}
                            className="text-red-600 hover:text-red-800 text-sm"
                          >
                            Hapus
                          </button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
              <div className="mt-4 text-right">
                <div className="text-xl font-bold text-gray-900">
                  Total: {formatCurrency(calculateTotal())}
                </div>
              </div>
            </div>
          )}

          <div className="flex gap-4">
            <button
              onClick={saveTransaction}
              className="bg-blue-600 text-white px-6 py-2 rounded-lg hover:bg-blue-700 transition-colors"
            >
              Simpan Transaksi
            </button>
            <button
              onClick={() => {
                setShowNewTransaction(false)
                setNewTransaction({ customerName: '', items: [], paymentMethod: 'Tunai' })
              }}
              className="bg-gray-500 text-white px-6 py-2 rounded-lg hover:bg-gray-600 transition-colors"
            >
              Batal
            </button>
          </div>
        </div>
      )}

      <div className="bg-white rounded-lg shadow-md border">
        <div className="px-6 py-4 border-b border-gray-200">
          <h2 className="text-xl font-semibold text-gray-900">Riwayat Transaksi</h2>
        </div>
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  ID
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Tanggal
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Pelanggan
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Total
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Pembayaran
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Status
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {transactions.map((transaction) => (
                <tr key={transaction.id} className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                    #{transaction.id}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                    {transaction.date}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                    {transaction.customerName}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                    {formatCurrency(transaction.total)}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                    {transaction.paymentMethod}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <span className="inline-flex px-2 py-1 text-xs font-semibold rounded-full bg-green-100 text-green-800">
                      {transaction.status}
                    </span>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  )
}
